  height: 408px;
  margin: 0 auto;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
  display: flex;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-size: cover;
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.card__title {
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  font-size: 10px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

