  align-items: center;
  gap: 4px;
  display: flex;
}

.card__review-counter {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  display: flex;
}

.card__price-text {
  font-size: 12px;
  line-height: 18px;
}

.card__price-count {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
